<script setup>
import Footer from '@/components/core/Footer.vue';

import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router';

const route = useRoute();

const maximized = ref([]);

function toggle(rating) {
  if (maximized.value.includes(rating)) {
    maximized.value.splice(maximized.value.indexOf(rating), 1);
  } else {
    maximized.value.push(rating);
  }
}

onMounted(() => {
  if (route.query.rating) {
    maximized.value.push(route.query.rating);
  }
});
</script>

